import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import axios from "axios";
import { useTheme } from "@material-ui/core/styles";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import { RichText } from "prismic-reactjs";
import Slider from "../Slider";
import { Typography, Container } from "@material-ui/core";
import Button from "@landingpage/core/src/components/Button";

import StyledComponent from "./Movies.style";

const columnMapping = {
  landscape: {
    xs: 3,
    sm: 3,
    md: 3,
    lg: 4,
    xl: 5,
  },
  portrait: {
    xs: 3,
    sm: 3,
    md: 5,
    lg: 6,
    xl: 10,
  },
};

const useWidth = () => {
  const theme = useTheme();
  const keys = [...theme.breakpoints.keys].reverse();
  return (
    keys.reduce((output, key) => {
      // eslint-disable-next-line react-hooks/rules-of-hooks
      const matches = useMediaQuery(theme.breakpoints.up(key));
      return !output && matches ? key : output;
    }, null) || "xs"
  );
};

const MovieSlider = ({ item }) => {
  const [collections, setCollections] = useState({});
  const width = useWidth();

  useEffect(() => {
    const api = axios.create({
      baseURL: process.env.GATSBY_APP_CATALOG_URL,
    });

    api
      .get(`v1/collection/${item.collection_id}`)
      .then((response) => setCollections(response.data));
  }, [item.collection_id]);

  if (!collections.items) return null;

  return (
    <div className="movieList" key={collections.id}>
      {item.collection_title && (
        <div style={{ justifyContent: "centepr", display: "flex" }}>
          <Typography variant="h3" className="title">
            {RichText.asText(item.collection_title)}
          </Typography>
        </div>
      )}
      <div className="fadeContainer">
        <div className="fadeEffect left" />
        <div className="fadeEffect right" />
        <Slider
          slidesToShow={columnMapping.portrait[width]}
          aspectRatio={1.44}
          heightMode="max"
        >
          {collections.items.map((movie) => {
            const listImage = `${process.env.GATSBY_APP_RESOURCES_URL}/${movie.id}/list/360x520/${movie.list_image}.jpeg`;
            const coverImage = `${process.env.GATSBY_APP_RESOURCES_URL}/${movie.id}/cover/360x520/${movie.cover_image}.jpeg`;

            return movie.list_image ? (
              <div className="root" key={movie.id}>
                <div
                  className="movie"
                  style={{ backgroundImage: `url(${listImage})` }}
                />
                <div className="container">
                  <div style={{ height: 50 }} />
                </div>
              </div>
            ) : (
              <div className="root" key={movie.id}>
                <div
                  className="movie"
                  style={{ backgroundImage: `url(${coverImage})` }}
                />
                <div className="container">
                  <div style={{ height: 50 }} />
                </div>
              </div>
            );
          })}
        </Slider>
      </div>
      {item.collection_text && (
        <Container maxWidth="lg" className="collectionText">
          <Typography variant="body1" component="div" gutterBottom>
            {RichText.render(item.collection_text)}
          </Typography>
        </Container>
      )}
    </div>
  );
};

const CallToActions = ({ item }) => {
  if (!item.cta_title) return null;

  const classNames = ["call-to-action"];

  if (item.cta_color !== "default") {
    classNames.push(item.cta_color);
  }
  return (
    <Button
      className={classNames.join(" ")}
      variant="contained"
      color="primary"
      href={item.cta_link.url}
      target={item.cta_link.target}
    >
      {RichText.asText(item.cta_title)}
    </Button>
  );
};

const Movies = ({ slice }) => {
  return (
    <StyledComponent>
      <Container maxWidth="xl" disableGutters className="slice-container">
        <Container maxWidth="lg" className="content">
          <Typography variant="h2">
            {RichText.asText(slice?.primary?.movie_title)}
          </Typography>

          <Typography
            variant="body1"
            component="div"
            gutterBottom
            className="contentText"
          >
            {RichText.render(slice?.primary?.movie_text)}
          </Typography>
        </Container>

        {slice.fields.map((item, index) => (
          <MovieSlider item={item} key={index} />
        ))}
        <Container maxWidth="lg" className="content">
          <CallToActions item={slice?.primary} />
        </Container>
      </Container>
    </StyledComponent>
  );
};

Movies.propTypes = {
  slice: PropTypes.shape({
    type: PropTypes.string.isRequired,

    primary: PropTypes.shape({
      movie_text: PropTypes.array.isRequired,
      movie_title: PropTypes.array.isRequired,
      items: PropTypes.arrayOf({}),
    }).isRequired,
  }).isRequired,
};

export default Movies;
