import React from "react";
import { RichText } from "prismic-reactjs";
import {
  useScrollTrigger,
  AppBar,
  Toolbar,
  Typography,
  Slide,
  Container,
  Grid,
} from "@material-ui/core";

import Button from "../Button";
import Logo from "@landingpage/app/src/images/logo.svg";

import StyledComponent from "./Topbar.style";

const HideOnScroll = (props) => {
  const { children, window } = props;

  const trigger = useScrollTrigger({ target: window ? window() : undefined });

  return (
    <Slide appear={false} direction="down" in={!trigger}>
      {children}
    </Slide>
  );
};

const Topbar = ({ topbar, logo, logoSquare }) => {
  if (!topbar) return null;

  return (
    <StyledComponent>
      <HideOnScroll>
        <AppBar className="app-bar" position="fixed" color="inherit">
          <Toolbar disableGutters>
            <Container maxWidth="xl" disableGutters>
              <Grid
                className="app-bar-container"
                container
                direction="row"
                justify="space-between"
                alignItems="center"
              >
                <Grid className="logo" item xs={false} sm={false}>
                  <Logo
                    width={logo.width}
                    height={logo.height}
                    className={logoSquare ? "logoSquare" : null}
                  />
                </Grid>
                <Grid className="info" item xs="auto" sm>
                  <Grid
                    container
                    direction="row"
                    justify="center"
                    alignItems="center"
                  >
                    <Grid item md>
                      {topbar.description && (
                        <Typography noWrap variant="h6">
                          {RichText.asText(topbar.description)}
                        </Typography>
                      )}
                    </Grid>
                    <Grid item xs={12} sm={12} md="auto">
                      {topbar.call_to_actions.map((callToAction, index) => (
                        <Button
                          key={index}
                          variant="outlined"
                          color="secondary"
                          href={callToAction.cta_link.url}
                          target={callToAction.cta_link.target}
                        >
                          {RichText.asText(callToAction.cta_title)}
                        </Button>
                      ))}
                    </Grid>
                  </Grid>
                </Grid>
              </Grid>
            </Container>
          </Toolbar>
        </AppBar>
      </HideOnScroll>
      <Toolbar />
    </StyledComponent>
  );
};

Topbar.defaultProps = {
  topbar: null,
  logo: { height: 80, width: 85 },
  logoSquare: false,
};

Topbar.propTypes = {};

export default Topbar;
