import React from "react";
import PropTypes from "prop-types";
import { RichText } from "prismic-reactjs";
import { Container, Grid, Paper, Typography } from "@material-ui/core";

import { Image } from "../Image";
import Button from "../Button";
import UspList from "./UspList";
import SelectPlan from "./SelectPlan";

import StyledComponent from "./Pricing.style";

const CtaButton = ({ slice }) => {
  if (!slice.cta_title || !slice.cta_link) return null;

  return (
    <Grid item xs={12}>
      <Button
        fullWidth
        variant="contained"
        color="primary"
        href={slice.cta_link.url}
        target={slice.cta_link.target}
      >
        {RichText.asText(slice.cta_title)}
      </Button>
    </Grid>
  );
};

const CustomImage = ({ image }) => {
  if (!image) return null;

  return (
    <Grid item xs={12}>
      <Image image={image} />
    </Grid>
  );
};

const Pricing = ({ slice, bottomMargin }) => {
  return (
    <StyledComponent className={bottomMargin ? "bottomMargin" : null}>
      <Container>
        <Paper className="pricing-card">
          <Grid container spacing={6}>
            <Grid item xs={12}>
              <Typography variant="h2">
                {RichText.asText(slice.primary.pricing_title)}
              </Typography>
            </Grid>

            <UspList items={slice.fields} />
            <SelectPlan {...slice.primary} />
            <CtaButton slice={slice.primary} />
            <CustomImage image={slice.primary.image} />
          </Grid>
        </Paper>
      </Container>
    </StyledComponent>
  );
};

Pricing.defaultProps = {
  slice: {},
  bottomMargin: false,
};

Pricing.propTypes = {
  slice: PropTypes.shape({
    primary: PropTypes.shape({
      pricing_title: PropTypes.array.isRequired,
    }).isRequired,

    items: PropTypes.arrayOf(
      PropTypes.shape({
        usp_title: PropTypes.array.isRequired,
      })
    ),
  }).isRequired,
  bottomMargin: PropTypes.bool,
};

export default Pricing;
