import React from "react";
import PropTypes from "prop-types";
import {
  Grid,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
} from "@material-ui/core";
import CheckIcon from "@material-ui/icons/Check";
import { RichText } from "prismic-reactjs";

import StyledComponent from "./UspList.style";

const UspColumn = ({ columns }) => {
  return columns.map((items, columnIndex) => (
    <Grid className="usp-list-container" key={columnIndex} item md={6}>
      <List dense disablePadding>
        {items.map((item, itemIndex) => (
          <ListItem key={itemIndex} disableGutters>
            <ListItemIcon>
              <CheckIcon />
            </ListItemIcon>
            <ListItemText primary={RichText.asText(item.usp_title)} />
          </ListItem>
        ))}
      </List>
    </Grid>
  ));
};

const UspList = ({ items }) => {
  if (!items) return null;

  const sliceIndex = Math.ceil(items.length / 2);
  const columns = [items.slice(0, sliceIndex), items.slice(sliceIndex)];

  return (
    <Grid item xs={12}>
      <StyledComponent>
        <Grid container direction="row" justify="center" spacing={2}>
          <UspColumn columns={columns} />
        </Grid>
      </StyledComponent>
    </Grid>
  );
};

UspList.defaultProps = {
  items: [],
};

UspList.propTypes = {
  items: PropTypes.arrayOf(
    PropTypes.shape({
      usp_title: PropTypes.array.isRequired,
    }).isRequired
  ),
};

export default UspList;
