import React from "react";
import PropTypes from "prop-types";
import { RichText } from "prismic-reactjs";
import { Grid, Radio, Typography } from "@material-ui/core";

import Button from "../Button";

import StyledComponent from "./SelectPlan.style";

const SelectPlanButton = ({
  index,
  title,
  price,
  ribbon,
  selected,
  onChange,
  currency_symbol,
}) => {
  const changePlan = () => {
    onChange(index);
  };

  return (
    <Button
      className={`plan-button ${selected ? "active" : ""}`}
      variant="outlined"
      color="secondary"
      fullWidth
      onClick={changePlan}
    >
      <Grid container direction="row" justify="flex-start" alignItems="center">
        <Grid item xs={9} sm>
          <Grid container justify="flex-start" alignItems="center">
            <Grid className="plan-name" item xs>
              <Typography variant="body1">{RichText.asText(title)}</Typography>
            </Grid>

            <Grid className="plan-price" item xs={false}>
              <Typography variant="body1">
                {currency_symbol || ""} {price}
              </Typography>
            </Grid>
          </Grid>
        </Grid>

        <Grid className="plan-radio" item xs={3} sm="auto">
          <Radio checked={selected === true} />
        </Grid>
      </Grid>

      {ribbon && <div className="ribbon">{RichText.asText(ribbon)}</div>}
    </Button>
  );
};

const SelectPlan = (props) => {
  const [selectedValue, setSelectedValue] = React.useState(
    props?.selected_plan === "monthly" ? 0 : 1
  );
  const changePlan = (plan) => {
    setSelectedValue(plan);
  };

  return (
    <Grid item xs={12}>
      <StyledComponent>
        <Grid container direction="row" justify="space-between" spacing={2}>
          <Grid item xs={12} sm={12} md={6}>
            <SelectPlanButton
              index={0}
              title={props.monthly_title}
              price={props.monthly_price}
              ribbon={props.monthly_ribbon}
              currency_symbol={props.currency_symbol}
              selected={selectedValue === 0}
              onChange={changePlan}
            />
          </Grid>
          <Grid item xs={12} sm={12} md={6}>
            <SelectPlanButton
              index={1}
              title={props.yearly_title}
              price={props.yearly_price}
              ribbon={props.yearly_ribbon}
              currency_symbol={props.currency_symbol}
              selected={selectedValue === 1}
              onChange={changePlan}
            />
          </Grid>
        </Grid>
      </StyledComponent>
    </Grid>
  );
};

SelectPlan.defaultProps = {
  currency_symbol: "",
};

SelectPlan.propTypes = {
  monthly_title: PropTypes.array.isRequired,
  monthly_price: PropTypes.number.isRequired,

  yearly_title: PropTypes.array.isRequired,
  yearly_price: PropTypes.number.isRequired,
  currency_symbol: PropTypes.string,
};

export default SelectPlan;
