import React from "react";
import PropTypes from "prop-types";
import { RichText } from "prismic-reactjs";
import { Container, Grid, Typography } from "@material-ui/core";
import {
  Accordion,
  AccordionSummary,
  AccordionDetails,
} from "@material-ui/core";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";

import Button from "../Button";

import StyledComponent from "./Faq.style";

const FaqItem = ({ question, description }) => {
  return (
    <Accordion className="expansion-panel">
      <AccordionSummary expandIcon={<ExpandMoreIcon />}>
        <Typography>{RichText.asText(question)}</Typography>
      </AccordionSummary>
      <AccordionDetails>
        <Typography component="div">{RichText.render(description)}</Typography>
      </AccordionDetails>
    </Accordion>
  );
};

const CtaButton = ({ slice }) => {
  if (!slice.cta_title || !slice.target) return null;

  return (
    <Grid item xs={12}>
      <Button
        fullWidth
        variant="contained"
        color="primary"
        href={slice.cta_link.url}
        target={slice.cta_link.target}
      >
        {RichText.asText(slice.cta_title)}
      </Button>
    </Grid>
  );
};

const Faq = ({ slice }) => {
  return (
    <StyledComponent>
      <Container>
        <Grid container spacing={4}>
          <Grid item xs={12}>
            <Typography variant="h2">
              {RichText.asText(slice.primary.faq_title)}
            </Typography>
          </Grid>

          <Grid item xs={12}>
            {slice.fields.map((item, index) => (
              <FaqItem
                key={index}
                question={item.faq_question}
                description={item.faq_description}
              />
            ))}
          </Grid>

          <Grid item xs={12}>
            <Container maxWidth="sm">
              <CtaButton slice={slice.primary} />
            </Container>
          </Grid>
        </Grid>
      </Container>
    </StyledComponent>
  );
};

Faq.defaultProps = {
  slice: {},
};

Faq.propTypes = {
  slice: PropTypes.shape({
    primary: PropTypes.shape({
      faq_title: PropTypes.array.isRequired,
    }).isRequired,

    items: PropTypes.arrayOf(
      PropTypes.shape({
        faq_question: PropTypes.array.isRequired,
        faq_description: PropTypes.array.isRequired,
      })
    ),
  }).isRequired,
};

export default Faq;
