import React from "react";
import PropTypes from "prop-types";
import { StaticQuery, graphql } from "gatsby";
import { RichText } from "prismic-reactjs";
import {
  useMediaQuery,
  Container,
  Grid,
  Typography,
  Link,
} from "@material-ui/core";
import moment from "moment";
import { withPreview } from "@prismicio/gatsby-source-prismic-graphql";

import Button from "../Button";

import StyledComponent from "./Footer.style";
import Logo from "@landingpage/app/src/images/logo.svg";

const query = graphql`
  query FooterMetaData {
    site {
      siteMetadata {
        siteUrl
        title
        description
        keywords
      }
    }
  }
`;

const Footer = ({ footer, publicationDate, logo, borderTop, spacingTop }) => {
  const isMobile = useMediaQuery((theme) => theme.breakpoints.down("sm"));

  if (!footer) return null;

  const classNames = [];
  const year = moment(publicationDate).year();

  if (borderTop) {
    classNames.push("borderTop");
  }

  if (spacingTop) {
    classNames.push("spacingTop");
  }

  return (
    <StaticQuery
      query={`${query}`}
      render={withPreview(
        (data) => (
          <StyledComponent className={classNames.join(" ")}>
            <Container maxWidth="xl" disableGutters>
              <Grid container alignItems="center">
                <Grid className="logo left" item xs={12} sm={12} md={3}>
                  <Logo width={logo.width} height={logo.height} />{" "}
                </Grid>
                <Grid item xs={12} sm={12} md={6}>
                  <Grid container alignItems="center">
                    <Grid className="links" item xs={12} sm={12}>
                      <nav>
                        <ul>
                          {footer.links.map((link, index) => (
                            <li key={index} className="center">
                              <Link
                                variant="body2"
                                color="textPrimary"
                                href={link.link.url}
                                target={link.link.target}
                              >
                                {RichText.asText(link.title)}
                              </Link>
                            </li>
                          ))}
                        </ul>
                      </nav>
                    </Grid>

                    {isMobile && (
                      <Grid className="button" item xs={12} sm={12}>
                        <Button
                          className="center"
                          variant="outlined"
                          color="secondary"
                          href={footer.cta_link.url}
                          target={footer.cta_link.target}
                        >
                          {RichText.asText(footer.cta_title)}
                        </Button>
                      </Grid>
                    )}

                    <Grid className="copyright" item xs={12} sm={12}>
                      <Typography className="center" variant="body2">
                        <small>
                          &copy;{year} {data.site.siteMetadata.title}
                        </small>
                      </Typography>
                    </Grid>
                  </Grid>
                </Grid>
                {!isMobile && (
                  <Grid
                    className="button right mobile-center"
                    item
                    xs={12}
                    sm={12}
                    md={3}
                  >
                    <Button
                      className="center"
                      variant="outlined"
                      color="secondary"
                      href={footer.cta_link.url}
                      target={footer.cta_link.target}
                    >
                      {RichText.asText(footer.cta_title)}
                    </Button>
                  </Grid>
                )}
              </Grid>
            </Container>
          </StyledComponent>
        ),
        query
      )}
    />
  );
};

Footer.defaultProps = {
  footer: null,
  borderTop: false,
  logo: { height: 80, width: 85 },
};

Footer.propTypes = {
  footer: PropTypes.object.isRequired,
};

export default Footer;
