import React from "react";
import PropTypes from "prop-types";

import Movies from "@landingpage/core/src/components/Movies";
import Pricing from "@landingpage/core/src/components/Pricing";
import Faq from "@landingpage/core/src/components/Faq";
import ContentBlock from "../ContentBlock";
import ContentWithQuoteBox from "../ContentWithQuoteBox";
import MovieHeader from "../MovieHeader";

import StyledComponent from "./Slices.style";

const Slices = ({ slices }) => {
  return (
    <StyledComponent>
      {slices.map((slice, index) => {
        switch (slice.type) {
          case "movieheader":
            return <MovieHeader key={index} slice={slice} />;
          case "movie_slider":
            return <Movies key={index} slice={slice} />;
          case "contentwithquote":
            return <ContentWithQuoteBox key={index} slice={slice} />;
          // case "pricing":
          //   return <Pricing key={index} slice={slice} bottomMargin />;
          case "faq":
            return <Faq key={index} slice={slice} />;
          case "contentblock":
            return <ContentBlock key={index} slice={slice} />;
          case "content1":
            return <ContentBlock key={index} slice={slice} />;

          default:
            return null;
        }
      })}
    </StyledComponent>
  );
};

Slices.defaultProps = {
  slices: [],
};

Slices.propTypes = {
  slices: PropTypes.arrayOf(
    PropTypes.shape({
      type: PropTypes.string.isRequired,
    }).isRequired
  ).isRequired,
};

export default Slices;
