import React from "react";
import { Container, Paper, Grid, Typography } from "@material-ui/core";
import { RichText } from "prismic-reactjs";
import PropTypes from "prop-types";

import {
  Image,
  ResponsiveBackgroundImage,
} from "@landingpage/core/src/components/Image";
import StyledComponent from "./ContentWithQuoteBox.style";

const ContentWithQuoteBox = ({ slice }) => {
  return (
    <StyledComponent>
      <ResponsiveBackgroundImage image={slice.primary.background} cover>
        <Container className="content">
          <Paper className="card">
            <Grid container spacing={6}>
              <Grid item xl={6} md={6} sm={12} xs={12}>
                <Typography variant="h2">
                  {RichText.asText(slice.primary.box_title)}
                </Typography>
                <Typography variant="body1" component="div" gutterBottom>
                  {RichText.render(slice.primary.box_text)}
                </Typography>
              </Grid>
              <Grid item xl={6} md={6} sm={12} xs={12} className="quoteContent">
                {slice.primary.quote_image && (
                  <Image className="image" image={slice.primary.quote_image} />
                )}
                {slice.primary.quote_text && (
                  <Typography variant="h4">
                    {RichText.asText(slice.primary.quote_text)}
                  </Typography>
                )}
                {slice.primary.quote_name_date && (
                  <Typography variant="body1" component="div" gutterBottom>
                    {RichText.render(slice.primary.quote_name_date)}
                  </Typography>
                )}
              </Grid>
            </Grid>
            <div className="ratingContent">
              <Image className="image" image={slice.primary.rating} />
            </div>
          </Paper>
        </Container>
      </ResponsiveBackgroundImage>
    </StyledComponent>
  );
};

ContentWithQuoteBox.defaultProps = {
  slice: {},
};

ContentWithQuoteBox.propTypes = {
  slice: PropTypes.shape({
    type: PropTypes.string.isRequired,

    primary: PropTypes.shape({
      box_title: PropTypes.array.isRequired,
      box_text: PropTypes.array.isRequired,
      quote_text: PropTypes.array,
      quote_name_date: PropTypes.array,
    }).isRequired,
  }).isRequired,
};

export default ContentWithQuoteBox;
