import React, { useState } from "react";
import PropTypes from "prop-types";
import { RichText } from "prismic-reactjs";
import {
  useMediaQuery,
  Container,
  Grid,
  Typography,
  Collapse,
} from "@material-ui/core";
import PlusIcon from "@material-ui/icons/Add";
import RemoveIcon from "@material-ui/icons/Remove";

import Button from "@landingpage/core/src/components/Button";
import {
  ResponsiveImage,
  ResponsiveBackgroundImage,
} from "@landingpage/core/src/components/Image";

import StyledComponent from "./ContentBlock.style";

const Text = ({
  slice,
  imageDirection,
  isMobile,
  readMore,
  onReadMoreClick,
}) => {
  let firstParagraph = slice.primary.content_description;
  let secondParagraph = null;

  if (
    slice.primary.read_more_button &&
    Array.isArray(slice.primary.content_description)
  ) {
    firstParagraph = slice.primary.content_description.slice(0, 1);

    if (slice.primary.content_description.length > 1) {
      secondParagraph = slice.primary.content_description.slice(-1);
    }
  }

  return (
    <React.Fragment>
      <Typography variant="h2">
        {RichText.asText(slice.primary.content_title)}
      </Typography>

      {!isMobile && imageDirection === "top" && (
        <ResponsiveImage
          className="image"
          image={slice.primary.content_image}
          params={{ sizes: "50%" }}
        />
      )}

      <Typography variant="body1" component="div">
        {RichText.render(firstParagraph)}
      </Typography>
      {slice.primary.read_more_button && secondParagraph && (
        <Collapse in={readMore}>
          <Typography variant="body1" component="div" gutterBottom>
            {RichText.render(secondParagraph)}
          </Typography>
        </Collapse>
      )}

      <Quote
        quote={slice.primary.quote_text}
        person={slice.primary.quote_person}
      />

      {isMobile && (
        <ResponsiveImage
          className="image"
          image={slice.primary.content_image}
        />
      )}

      <div className="buttons">
        {slice.primary.read_more_button && secondParagraph && (
          <ReadMoreButton
            item={slice.primary}
            onClick={onReadMoreClick}
            readMore={readMore}
          />
        )}
        <CallToActions items={slice.fields} />
      </div>

      {!isMobile && imageDirection === "bottom" && (
        <ResponsiveImage
          className="image"
          image={slice.primary.content_image}
          params={{ sizes: "50%" }}
        />
      )}
    </React.Fragment>
  );
};

const Quote = ({ quote, person }) => {
  if (!quote) return null;

  return (
    <React.Fragment>
      <Typography className="quote-text" variant="h4" component="div">
        {RichText.render(quote)}
      </Typography>
      <Typography className="quote-person" variant="body1" gutterBottom>
        <small>{RichText.asText(person)}</small>
      </Typography>
    </React.Fragment>
  );
};

const ReadMoreButton = ({ item, onClick, readMore }) => {
  if (!readMore) {
    return (
      <Button
        className="call-to-action readMoreButton"
        variant="outlined"
        color="default"
        onClick={onClick}
      >
        {RichText.asText(item.read_more_text)}
        <PlusIcon className="readMoreIcon" />
      </Button>
    );
  }

  return (
    <Button
      className="call-to-action readMoreButton"
      variant="outlined"
      color="default"
      onClick={onClick}
    >
      {RichText.asText(item.read_less_text)}
      <RemoveIcon className="readMoreIcon" />
    </Button>
  );
};

const CallToActions = ({ items }) => {
  if (items.length === 0) return null;

  return items.map((callToAction, index) => {
    const classNames = ["call-to-action"];

    if (callToAction.cta_color !== "default") {
      classNames.push(callToAction.cta_color);
    }

    if (!callToAction.cta_title || !callToAction.cta_link) return null;

    return (
      <Button
        className={classNames.join(" ")}
        key={index}
        variant="contained"
        color="primary"
        href={callToAction.cta_link.url}
        target={callToAction.cta_link.target}
      >
        {RichText.asText(callToAction.cta_title)}
      </Button>
    );
  });
};

const columnSizes = (sizes, inverse = false) => {
  const calculateSize = (size, inverse) => {
    if (inverse && size < 12) {
      return 12 - size;
    }

    return size;
  };

  return {
    xs: calculateSize(sizes.xs, inverse),
    sm: calculateSize(sizes.sm, inverse),
    md: calculateSize(sizes.md, inverse),
    lg: calculateSize(sizes.lg, inverse),
    xl: calculateSize(sizes.xl, inverse),
  };
};

const ContentBlock = ({ slice }) => {
  const isMobile = useMediaQuery((theme) => theme.breakpoints.down("sm"));
  const [readMore, setReadMore] = useState(false);

  const imageDirection = slice.primary.image_direction;
  const imageType = slice.primary.image_type;

  let columns = {
    xs: parseInt(slice?.primary?.grid_xs) || 12,
    sm: parseInt(slice?.primary?.grid_small) || 12,
    md: parseInt(slice?.primary?.grid_medium) || 7,
    lg: parseInt(slice?.primary?.grid_large) || 6,
    xl: parseInt(slice?.primary?.grid_xl) || 5,
  };

  let classNames = [
    `image-${imageDirection}`,
    `image-${imageType}`,
    "slice-container",
    "mobile-center",
  ];

  let direction = "row";
  let position = "center right";

  if (["left", "top"].indexOf(imageDirection) > -1) {
    direction = "row-reverse";
    position = "center left";
  }
  if (["bottom", "top"].indexOf(imageDirection) > -1) {
    columns = {
      ...columns,
      md: 12,
      lg: 12,
      xl: 12,
    };
    position = "center bottom";
    classNames.push("center");
  }

  const onReadMoreClick = () => {
    setReadMore(!readMore);
  };

  return (
    <StyledComponent className={readMore ? "open" : ""}>
      <Container
        maxWidth="xl"
        disableGutters
        className={`background-${slice.primary.background_color || "default"}`}
      >
        {imageType === "fade" && !isMobile && (
          <ResponsiveBackgroundImage
            image={slice.primary.content_image}
            position={position}
          >
            <Grid
              className={classNames.join(" ")}
              container
              direction={direction}
              justify="space-between"
              alignItems="center"
            >
              <Grid className="text" item {...columnSizes(columns)}>
                <Text
                  slice={slice}
                  readMore={readMore}
                  onReadMoreClick={onReadMoreClick}
                />
              </Grid>
            </Grid>
          </ResponsiveBackgroundImage>
        )}

        {imageType === "default" &&
          ["right", "left"].indexOf(imageDirection) > -1 &&
          !isMobile && (
            <Grid
              className={classNames.join(" ")}
              container
              direction={direction}
              justify="space-between"
              alignItems="center"
            >
              <Grid className="text" item {...columnSizes(columns)}>
                <Text
                  slice={slice}
                  readMore={readMore}
                  onReadMoreClick={onReadMoreClick}
                />
              </Grid>
              <Grid className="image" item {...columnSizes(columns, true)}>
                <ResponsiveImage image={slice.primary.content_image} />
              </Grid>
            </Grid>
          )}

        {imageType === "default" &&
          ["top", "bottom"].indexOf(imageDirection) > -1 &&
          !isMobile && (
            <Grid className={classNames.join(" ")} container>
              <Grid className="text" item xs={12}>
                <Text
                  imageDirection={imageDirection}
                  slice={slice}
                  readMore={readMore}
                  onReadMoreClick={onReadMoreClick}
                />
              </Grid>
            </Grid>
          )}

        {isMobile && (
          <Grid
            className={classNames.join(" ")}
            container
            direction={direction}
            alignItems="center"
          >
            <Grid className="text" item {...columnSizes(columns)}>
              <Text
                isMobile={true}
                slice={slice}
                readMore={readMore}
                onReadMoreClick={onReadMoreClick}
              />
            </Grid>
          </Grid>
        )}
      </Container>
    </StyledComponent>
  );
};

ContentBlock.defaultProps = {
  slice: {},
};

ContentBlock.propTypes = {
  slice: PropTypes.shape({
    type: PropTypes.string.isRequired,

    primary: PropTypes.shape({
      content_title: PropTypes.array.isRequired,
      content_description: PropTypes.array.isRequired,

      quote_text: PropTypes.array,
      quote_person: PropTypes.array,

      read_more_button: PropTypes.bool,
    }).isRequired,

    items: PropTypes.arrayOf(
      PropTypes.shape({
        cta_title: PropTypes.array.isRequired,
        cta_link: PropTypes.shape({
          url: PropTypes.string,
          target: PropTypes.string,
        }),
      })
    ),
  }).isRequired,
};

export default ContentBlock;
