import React from "react";
import { RichText } from "prismic-reactjs";
import { useMediaQuery, Container, Grid, Typography } from "@material-ui/core";
import Button from "@landingpage/core/src/components/Button";

import {
  Image,
  ResponsiveBackgroundImage,
} from "@landingpage/core/src/components/Image";

import StyledComponent from "./MovieHeader.style";

const MovieHeader = ({ slice }) => {
  const isMobile = useMediaQuery((theme) => theme.breakpoints.down("sm"));

  return (
    <StyledComponent>
      <ResponsiveBackgroundImage
        className="backgroundImage"
        image={slice.primary.background}
        position="bottom left"
        cover
      >
        <Container maxWidth="xl" disableGutters className="header">
          <Grid container>
            <Grid item md={5} xs={12} sm={12} className="contentCenter">
              {isMobile && (
                <div className="coverContainer">
                  <Image image={slice.primary.cover} />
                </div>
              )}
              <div className="titleAndSubtitle">
                <Typography variant="h1">
                  {RichText.asText(slice?.primary?.header_title)}
                </Typography>
                <Typography>
                  {RichText.asText(slice?.primary?.sub_title)}
                </Typography>
              </div>

              <Typography variant="body2" component="div">
                {RichText.render(slice?.primary?.description)}
              </Typography>

              <Typography variant="body2" component="div">
                {RichText.render(slice?.primary?.sub_description)}
              </Typography>
              <div>
                <Button
                  variant="contained"
                  color="primary"
                  href={slice?.primary?.cta_link.url}
                  target={slice?.primary?.target}
                >
                  {RichText.asText(slice?.primary?.cta_title)}
                </Button>
              </div>
            </Grid>
            {!isMobile && (
              <Grid
                item
                md={7}
                xs={12}
                sm={12}
                className="coverImage contentCenter"
              >
                <div className="coverContainer">
                  <Image image={slice.primary.cover} />
                </div>
              </Grid>
            )}
          </Grid>
        </Container>
      </ResponsiveBackgroundImage>
    </StyledComponent>
  );
};

MovieHeader.defaultProps = {
  slice: {},
};

MovieHeader.propTypes = {};

export default MovieHeader;
